.form {
    max-width: 30rem;
    margin: 2.5rem auto;
}

.form > div {
    display: flex;
    flex-direction: column;
    margin: 1rem;
}

.form label {
    margin: 1rem 0;
    color: blueviolet;
    font-size: 1.1rem;
}

.btn--primary {
    background-color: cyan;
    border: none;
    border-radius: 5px;
    padding: 5px 0;
}

.btn--primary:hover {
    cursor: pointer;
}

.form input {
    border-radius: 5px;
    outline: none;
    border: 1px solid;
    padding: 5px 0px 5px 10px;
}

.form h1 {
    text-align: center;
    font-size: 26px;
    color: blueviolet;
}

.gradient-text {
    background: linear-gradient(to right, cyan, blueviolet);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.form button {
    margin-top: 10px;
    background: linear-gradient(to right, blueviolet, cyan);
    font-size: 1rem;
    color: white;
    padding: 10px;
}

.payment-method {
    flex-direction: row !important;
    align-items: center;
}

.payment-method input {
    margin-right: 10px;
}

.payment-method label {
    margin: 0;
}

.form .cart-title {
    margin-bottom: 50px;
}
