.row.checkout-steps {
    display: flex;
    max-width: 600px;
    margin: 0 auto;
}

.checkout-steps > div {
    border-top: 3px #c0c0c0 solid;
    color: #c0c0c0;
    flex: 1;
    padding: 10px;
    text-align: center;
}

.checkout-steps > div.active {
    border-top-color: #f08000;
    color: #f08000;
}
